main {
  overflow-x: hidden;
  align-items: center;
  align-content: center;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 0px;
  background: var(--backgroundMain);
  background-size: 250%;
  padding-bottom: 50px;
}

main .cartaBox {
  width: 60%;
  display: flex;
  margin: 15px;
  margin-bottom: 0;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}


main .cartaBox .ediblesSection {
  display: flex;
  width: fit-content;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-wrap: nowrap;
  flex-direction: column;
  gap: 10px;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 8px 10px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 13px;
  animation: reveal linear both;
  animation-timeline: view();
  animation-range: entry 25% cover 35%;
  background-color: var(--btnBG);
}

@keyframes reveal {
  from {
    opacity: 0;
    translate: 0 100px;

  }

  to {
    opacity: 1;
    translate: 0 0;
  }
}

main .titleCarta {
  display: flex;
  margin-top: 30px;
  width: 60%;
  align-items: center;
  align-content: center;
  justify-content: center;
  flex-wrap: nowrap;
  flex-direction: column;
  color: white;

}

main .cartaBox .edibleRow {
  align-items: center;
  align-content: center;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  flex-direction: row;
  margin: 10px;
  gap: 10px;

}

main .cartaBox .ediblesBox {


  background-color: var(--ediblesBG);
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 13px;

  height: calc(0px);
  overflow: hidden;

  margin: 0;
  width: min-content;
}

main .cartaBox .open {
  transition: all 0.5s ease-in-out;
  height: auto;
  max-height: 100%;
  width: fit-content;
  padding: 10px;
}


main .cartaBox .ediblesSection .itemName {
  flex-basis: 80%;
  align-items: flex-start;
  align-content: flex-start;
  display: flex;
  text-align: left;

}

main .cartaBox .ediblesSection .description {
  flex-basis: 70%;
  display: flex;
  align-items: flex-start;
}

main .cartaBox .ediblesSection .price {
  flex-basis: 15%;
  min-width: 65px;
}

